<template>
  <div class="indexMy">
  <My></My>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import My from './My';
export default {
  name: 'indexMy',
  components:{
    My
  },
  methods: {
    ...mapActions({
      changeIndexStatus: 'changeIndexStatus'
    }),
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.changeIndexStatus('my');
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



