<template>
 <div class="my">
    <div class="upload">
      <img class="bg" src="../assets/my_background.png" alt="">
      <input id="fileUploader" type="file" @change="upload($event)">
      <img class="avator" :src="avatar" alt="">
    </div>
    <div class="mybody">
      <router-link to="/messageCenter" class="bodyItem">
        <img src="../assets/message.png" alt="">
        <p>消息中心</p>
      </router-link>
      <router-link to="/chooseBeCared?clickType=my" class="bodyItem">
        <img src="../assets/beCared.png" alt="">
        <p>被护理人管理</p>
      </router-link>
      <router-link to="" class="bodyItem">
        <img src="../assets/servicesIntroduce.png" alt="">
        <p>服务介绍</p>
      </router-link>
      <router-link to="/more" class="bodyItem">
        <img src="../assets/more.png" alt="">
        <p>更多</p>
      </router-link>
    </div>  
 </div>

</template>
<script>
  import { mapGetters, mapActions} from 'vuex'
  import { Toast } from 'mint-ui';
  import {oneMonthTime, timeArr, dayArr} from '../utils/oneMonthTime'
export default {
  name: 'my',
  data() {
      return {
        avatar: ''
    }
  },
  computed: {
    ...mapGetters({
      // avatar: 'getAvatar'
    })
  },
  methods: {
    ...mapActions({
      uploadAvatar: 'uploadAvatar',
      changeAvatar: 'changeAvatar'
    }),
    upload: function(e) {
      let formData = new FormData();
      formData.append("avatar", e.target.files[0]);
      this.uploadAvatar(formData).then(response => {
        this.avatar = sessionStorage.getItem(response.data.avatar);
        sessionStorage.setItem('avatar', response.data.avatar);
        Toast({
          message: '修改成功',
          position: 'bottom',
          duration: 1000
        });
        //this.changeAvatar(response.data.avatar);
      }, error => {
        if(error.body.code == 3001) {
          this.$router.push('/');
        }
      })
    }
  },
  created() {
    this.avatar = sessionStorage.getItem('avatar');
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.my {
  height: 92%;
  width: 100%;
}
.upload {
  position: relative;
  width: 100%;
  height: 30%;
}
.upload .bg {
  height: 5rem;
  width: 100%;
}
.upload img.avator {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 2rem;
  height: 2rem;
  border-radius: 1rem;
  z-index: 0;
}
#fileUploader {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 3rem;
  height: 3rem;
  z-index: 1;
  opacity: 0;
}
.mybody {
  display: flex;
  flex-wrap: wrap;
  height: 70%;
  margin-top: 5%;
}
.bodyItem {
  width: 50%;
  height: 50%;
  display: block;
  text-decoration: none;
  color: black;
}
.bodyItem img {
  width: 50%;
  margin-left: 25%;
  margin-right: 25%;
}
.bodyItem p {
  text-align: center;
  font-size: 0.4rem;
}
</style>



